import React from 'react'
import {FaEnvelope} from "react-icons/fa"

export default function Contact({email}) {
    return (
        <section className="contact">
            <h2>Travaillons ensemble</h2>
            <p>Un projet à me confier? N'hésitez pas à me contacter.<br/>
            Décrivez moi vos besoins au mieux. Je reviendrai vers vous au plus vite.</p>
            <button onClick={() => navigator.clipboard.writeText(email)} title="Cliquez pour copier l'adresse e-mail"><FaEnvelope className="contact__address-icon"/>{email}</button>
        </section>
    )
}
